import UiRow from "./UiRow";
import { useGameState, useDispatch, useGameContext, useSquareSize } from "./UiGame";
import { useState, useEffect } from "react";

function range(size) {
    const a = [];
    for (let i = 0; i < size; i++) {
        a.push(i);
    }
    return a;
}

const BLACK_PLAYER_IMAGE = `${process.env.PUBLIC_URL}/black.png`;
const WHITE_PLAYER_IMAGE = `${process.env.PUBLIC_URL}/white.png`;
const BLACK_PLAYER = "Black";
const WHITE_PLAYER = "White";

export default function UiBoard() {
    const state = useGameState();
    const dispatch = useDispatch();
    const game = useGameContext();

    const [autoPlacement, setAutoPlacement] = useState(false);
    const [autoPlacementDelay, setAutoPlacementDelay] = useState(2000);

    useEffect(() => {
        if (!game.supportAutoPlacement() || !autoPlacement) return;

        let timeoutId = null;
        const coordinate = game.autoDetermineNextStoneCoordinate(state);
        if (coordinate) {
            timeoutId = setTimeout(() => {
                dispatch({ type: "placeStone", coordinate: coordinate });
            }, autoPlacementDelay);
        }
        return () => {
            if (timeoutId) clearTimeout(timeoutId);
        };
    }, [game, state, dispatch, autoPlacement, autoPlacementDelay]);

    let currentPlayerImage;
    let currentPlayer;
    let nextPlayerImage;
    let nextPlayer;

    if (state.isNextBlack) {
        currentPlayerImage = WHITE_PLAYER_IMAGE;
        currentPlayer = WHITE_PLAYER;
        nextPlayerImage = BLACK_PLAYER_IMAGE;
        nextPlayer = BLACK_PLAYER;
    } else {
        currentPlayerImage = BLACK_PLAYER_IMAGE;
        currentPlayer = BLACK_PLAYER;
        nextPlayerImage = WHITE_PLAYER_IMAGE;
        nextPlayer = WHITE_PLAYER;
    }

    const squareSize = useSquareSize();
    const boardPadding = squareSize / 4;
    const fontSize = squareSize / 2.2;
    const statusHeight = squareSize * 0.8;

    const restartButton = (
        <button
            style={{ fontSize: fontSize, marginLeft: squareSize / 2, width: squareSize * 3, height: statusHeight }}
            onClick={() => {
                if (state.hasWinner || window.confirm("Restart the game?")) dispatch(game.createRestartAction());
            }}
        >
            Restart
        </button>
    );

    return (
        <>
            <div className="status" style={{ width: "100%", fontSize: fontSize, marginBottom: fontSize }}>
                {state.hasWinner && (
                    <div>
                        Winner&nbsp;
                        <img
                            src={currentPlayerImage}
                            alt={currentPlayer}
                            style={{ width: statusHeight, height: statusHeight }}
                        />
                    </div>
                )}
                {!state.hasWinner && (
                    <div>
                        <img
                            src={nextPlayerImage}
                            alt={nextPlayer}
                            style={{ width: statusHeight, height: statusHeight }}
                        />
                    </div>
                )}
                <div>
                    {state.previousState && !state.hasWinner && (
                        <button
                            style={{
                                fontSize: fontSize,
                                width: squareSize * 5,
                                height: statusHeight,
                                marginLeft: squareSize / 2
                            }}
                            onClick={() => dispatch(game.createRollbackAction())}
                        >
                            Back
                        </button>
                    )}
                    {state.previousState && restartButton}
                </div>
            </div>
            <div className={game.getName() + " board"} style={{ width: "100%", padding: boardPadding }}>
                {range(game.ROW_COUNT).map((row) => (
                    <UiRow row={row} key={"row" + row} height={squareSize} />
                ))}
            </div>
            {game.supportAutoPlacement() && (
                <div
                    className="auto"
                    style={{
                        marginTop: squareSize / 2,
                        fontSize: fontSize
                    }}
                >
                    <div>
                        <label style={{ width: 3 * squareSize }}>
                            <input
                                type="checkbox"
                                checked={autoPlacement}
                                value="1"
                                onChange={(e) => console.log("Checkbox onChange", e.target.checked)}
                                // onChange works locally but won't be fired on github pages
                                onClick={(e) => {
                                    setAutoPlacement(e.target.checked);
                                }}
                            ></input>
                            Auto
                        </label>
                    </div>
                    {autoPlacement && (
                        <div>
                            <input
                                type="range"
                                min="100"
                                max="5000"
                                value={autoPlacementDelay}
                                step="100"
                                onChange={(e) => setAutoPlacementDelay(e.target.value)}
                                style={{ width: squareSize * (game.COLUMN_COUNT - 5.5) }}
                            ></input>
                            <span style={{ marginLeft: squareSize / 4 }}>{autoPlacementDelay}ms</span>
                        </div>
                    )}
                </div>
            )}
        </>
    );
}
